:root {
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-color: #333;
  justify-content: center;
  align-items: center;
  font-family: Ubuntu, sans-serif;
  display: flex;
  overflow: hidden;
}

.card {
  width: 45rem;
  height: 50rem;
  margin: 2rem 4rem;
  position: relative;
}

.one {
  background-image: url("../../../../forest-56930_1280.2565aa87.jpg");
  transition: all .5s;
}

.two {
  background-image: url("../../../../valley-4646114_1920.8f6a6661.jpg");
  transition: all .5s;
}

.three {
  background-image: url("../../../../namibia-4965456_1920.77d388f7.jpg");
}

.img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  border-radius: 20px;
}

.text {
  text-align: center;
  opacity: 0;
  background-image: linear-gradient(315deg, #fee2f8 0%, #dcf8ef 75%);
  border-radius: 20px;
  padding: 3rem;
  transition: all .5s;
  position: absolute;
  inset: 0;
}

h3 {
  margin-top: 20rem;
  margin-bottom: 2rem;
  font-size: 3rem;
}

p {
  font-size: 1.6rem;
}

.text-one {
  transform: rotateX(-180deg);
}

.card:hover .text {
  transform: rotateX(0);
  opacity: 1;
}

.card:hover .one {
  transform: rotateX(180deg);
}

.text-two {
  transform: rotateY(-180deg);
}

.card:hover .text {
  transform: rotateY(0);
  opacity: 1;
}

.card:hover .two {
  transform: rotateY(180deg);
}

@keyframes down {
  0% {
    z-index: -1;
    transform: translateY(0);
  }

  50% {
    z-index: -1;
    transform: translateY(60%);
  }

  100% {
    z-index: 1;
  }
}

@keyframes up {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  50% {
    transform: translateY(-60%);
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.card:hover .text-three {
  animation: textRotateX 1s;
}

.card:hover .three {
  animation: imageRotateX 1s;
}

@keyframes textRotateX {
  0% {
    z-index: -1;
    transform: translateX(0);
  }

  50% {
    z-index: -1;
    transform: translateX(60%);
  }

  100% {
    z-index: 1;
  }
}

@keyframes imageRotateX {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  50% {
    transform: translateX(-60%);
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

